* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root, .App {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.control-buttons {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  background: rgba(0,0,0,0.7);
  padding: 10px 20px;
  border-radius: 30px;
  color: white;
  display: flex;
  gap: 10px;
}

.control-buttons button {
  padding: 8px 16px;
  border-radius: 20px;
  border: none;
  background: #4CAF50;
  color: white;
  cursor: pointer;
  font-weight: bold;
}

.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* Garante que o vídeo da câmera ocupe todo o container */
video, canvas {
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  display: block !important;
}

/* FORÇAR VÍDEO PARA A DIREITA (remove faixa lateral) */
video, canvas {
  position: absolute !important;
  left: auto !important;
  right: 0 !important;
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  transform: translateX(0) !important;
}

/* FORÇAR OCUPAÇÃO VERTICAL TOTAL (remove faixa inferior) */
html, body, #root, .App, .container {
  height: 100% !important;
  min-height: 100% !important;
  max-height: 100% !important;
  position: fixed !important;
  top: 0 !important;
  bottom: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
}

/* GARANTIR QUE O VÍDEO PREENCHA A ALTURA */
video, canvas {
  top: 0 !important;
  bottom: 0 !important;
  height: 100% !important;
  min-height: 100% !important;
  max-height: 100% !important;
  object-fit: cover !important;
}
